import { LoadingController, ModalController } from "ionic-angular";
import { Component } from "@angular/core";
import {
  IonicPage,
  NavController,
  NavParams,
  ViewController
} from "ionic-angular";
import { JobLoopViewModel } from "../../Model/ViewModel/JobLoopViewModel";
import { GlobalComponent } from "../../components/global/global";
import { JobLoopServicesProvider } from "../../providers/job-loop-services/job-loop-services";

/**
 * Generated class for the JobLoopModalPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: "page-job-loop-add-edit-modal",
  templateUrl: "job-loop-add-edit-modal.html"
})
export class JobLoopAddEditModalPage {
  item: any;
  mode: string;
  change_mode: string;
  CanEditLoopJob: boolean;
  title: string;
  Job_type: string;
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public viewCtrl: ViewController,
    public loadingCtrl: LoadingController,
    public modalCtrl: ModalController,
    private global: GlobalComponent,
    private JobLoopServices: JobLoopServicesProvider,
  ) {
    this.item = new JobLoopViewModel();
    this.item.loop_seq = navParams.data.item.loop_seq;
    this.item.ssis_job_no = navParams.data.item.ssis_job_no;
    this.item.job01 = navParams.data.item.job01;
    this.item.job02 = navParams.data.item.job02;
    this.item.job03 = navParams.data.item.job03;
    this.item.exec_seq = navParams.data.item.exec_seq;
    this.item.is_active = navParams.data.item.is_active;
    this.item.job_loop_desc = navParams.data.item.job_loop_desc;
    this.item.remote_conn_id = navParams.data.item.remote_conn_id;
    this.item.last_exec_date = navParams.data.item.last_exec_date;
    this.item.last_exec_status_key = navParams.data.item.last_exec_status_key;
    this.item.creator = navParams.data.item.creator;
    this.item.create_time = navParams.data.item.create_time;
    this.item.modifier = navParams.data.item.modifier;
    this.item.last_update_time = navParams.data.item.last_update_time;
    this.item.remote_conn_string = navParams.data.item.remote_conn_string;
    this.CanEditLoopJob = navParams.data.CanEditLoopJob;
    this.mode = navParams.data.mode;
    this.change_mode = navParams.data.change_mode;
    this.title = this.item.ssis_job_no;


    if (this.mode==='POST' ) {
      this.get_max_loop_seq();
      this.get_max_exec_seq();
    }
  }

  get_max_loop_seq() {
    this.global.createLoader("取得最大值中...");
    this.global.loading.present().then(() => {
      this.JobLoopServices.GetMaxLoopSeqAsync(
        this.item.ssis_job_no
      ).subscribe(
        data => {
          if (data.DidError === true) {
            this.global.dismissLoading();
            this.global.showError(data.ErrorMessage);
          } else {
            this.item.loop_seq = data.Model;
            this.global.dismissLoading();
          }
        },
        err => {
          this.global.dismissLoading();
          this.global.showError("無法連上WebAPI伺服器-"+err.message);
        }
      );
    });
  }
  get_max_exec_seq() {
    this.global.createLoader("取得最大值中...");
    this.global.loading.present().then(() => {
      this.JobLoopServices.GetMaxExecSeqAsync(
        this.item.ssis_job_no
      ).subscribe(
        data => {
          if (data.DidError === true) {
            this.global.dismissLoading();
            this.global.showError(data.ErrorMessage);
          } else {
            this.item.exec_seq = data.Model;
            this.global.dismissLoading();
          }
        },
        err => {
          this.global.dismissLoading();
          this.global.showError("無法連上WebAPI伺服器-"+err.message);
        }
      );
    });
  }
  SelectConn() {
    let modal = this.modalCtrl.create("ConnSelectModalPage", {
      select_remote_conn_id: this.item.remote_conn_id,
      conn_type_key:"",
    });
    modal.onDidDismiss(select_data => {
      if (select_data == null) return;
      this.item.remote_conn_id = select_data.remote_conn_id;
      this.item.remote_conn_string = select_data.conn_string;
    });

    modal.present();
  }
  Save() {
    this.viewCtrl.dismiss(this.item);
  }
  close() {
    this.viewCtrl.dismiss();
  }
  ionViewDidLoad() {
    console.log("ionViewDidLoad JobLoopModalPage");
  }
}



