import { JobLoopListResponse } from "./../../Model/Response/JobLoopListResponse";
import { String } from "./../../Model/String";
import { Injectable } from "@angular/core";
import { JobLoopViewModel } from "../../Model/ViewModel/JobLoopViewModel";
import { JobLoopResponse } from "../../Model/Response/JobLoopResponse";
import { ServicesProvider } from "../services/services";
import { StringResponse } from "../../Model/Response/StringResponse";
import { NumberResponse } from "../../Model/Response/NumberResponse";

/*
  Generated class for the jobloopServicesProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class JobLoopServicesProvider {
  ctl: string;
  jobloops: JobLoopListResponse;
  jobloop: JobLoopResponse;
  constructor(
    private Services: ServicesProvider
  ) {
    // this.originUrl =apiUrl;
    this.ctl = "jobloop";

    console.log("Hello jobloopServicesProvider Provider");
  }

  GetListsAsync(ssis_job_no: string,PageSize: number, PageNumber: number, keyword: string,order_type:boolean) {
    var sub_url: string;
    sub_url = "jobloop?PageSize={1}&PageNumber={2}&keyword={3}&ssis_job_no={4}&order_type={5}";
    sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword, ssis_job_no,order_type);
   return this.Services.GetAsync(sub_url,this.ctl,true)
      .map(item => {
        this.jobloops = new JobLoopListResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          PageSize: item.PageSize,
          PageNumber: item.PageNumber,
          TotalRows: item.TotalRows,
          PageRows: item.PageRows,
          TotalPages: item.TotalPages,
          Model: item.Model
        });
        return this.jobloops;
      });
  }

  public PostPutAsync(JobLoopViewModel:JobLoopViewModel,mode:string) {
    var sub_url: string;
    if(mode==="POST")
      sub_url = "JobLoop";
    else
      sub_url = "JobLoop/" + JobLoopViewModel.loop_seq;
    return this.Services.PostPutAsync(sub_url, this.ctl, true,JobLoopViewModel,mode).map(
      item => {
        this.jobloop = new JobLoopResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.jobloop;
      }
    );
  }

  public DeleteAsync(loop_seq:number) {
    var sub_url: string;
    sub_url = "JobLoop/"+loop_seq;
    return this.Services.DeleteAsync(sub_url, this.ctl, true).map(
      item => {
        this.jobloop = new JobLoopResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.jobloop;
      }
    );
  }
  public GetSqlAsync(loop_seq:number,method:string) {
    var sub_url: string;
    sub_url = "JobLoop/get_{2}_sql/{1}";
    sub_url = String.Format(sub_url, this.ctl,loop_seq,method);

    return this.Services.GetAsync(sub_url, this.ctl, true).map(
      item => {
        return  new StringResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
      }
    );
  }

  public GetMaxLoopSeqAsync(ssis_job_no:string) {
    var sub_url: string;
    sub_url = "JobLoop/get_max_loop_seq?ssis_job_no={1}";
    sub_url = String.Format(sub_url, this.ctl,ssis_job_no);

    return this.Services.GetAsync(sub_url, this.ctl, true).map(
      item => {
        return  new NumberResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
      }
    );
  }

  public GetMaxExecSeqAsync(ssis_job_no:string) {
    var sub_url: string;
    sub_url = "JobLoop/get_max_exec_seq?ssis_job_no={1}";
    sub_url = String.Format(sub_url, this.ctl,ssis_job_no);

    return this.Services.GetAsync(sub_url, this.ctl, true).map(
      item => {
        return  new NumberResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
      }
    );
  }


  public unzip_pwd(JobLoopViewModel:JobLoopViewModel) {
    var sub_url: string;
    sub_url = "JobLoop/unzip_pwd/{1}";
    sub_url = String.Format(sub_url, this.ctl,JobLoopViewModel.loop_seq);

    return this.Services.PutAsync(sub_url, this.ctl, true,JobLoopViewModel).map(
      item => {
        return  new StringResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
      }
    );
  }

  public zip_pwd(JobLoopViewModel:JobLoopViewModel) {
    var sub_url: string;
    sub_url = "JobLoop/zip_pwd/{1}";
    sub_url = String.Format(sub_url, this.ctl,JobLoopViewModel.loop_seq);

    return this.Services.PutAsync(sub_url, this.ctl, true,JobLoopViewModel).map(
      item => {
        return  new StringResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
      }
    );
  }

  public clear_unzip_pwd(JobLoopViewModel:JobLoopViewModel) {
    var sub_url: string;
    sub_url = "JobLoop/clear_unzip_pwd/{1}";
    sub_url = String.Format(sub_url, this.ctl,JobLoopViewModel.loop_seq);

    return this.Services.PutAsync(sub_url, this.ctl, true,JobLoopViewModel).map(
      item => {
        return  new StringResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
      }
    );
  }
  public clear_zip_pwd(JobLoopViewModel:JobLoopViewModel) {
    var sub_url: string;
    sub_url = "JobLoop/clear_zip_pwd/{1}";
    sub_url = String.Format(sub_url, this.ctl,JobLoopViewModel.loop_seq);

    return this.Services.PutAsync(sub_url, this.ctl, true,JobLoopViewModel).map(
      item => {
        return  new StringResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
      }
    );
  }
}



